.changing-text-container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 60px; /*Adjust the height as needed*/
  position: relative;
}
.blank-space{
  margin:25%;
}
.changing-text {
  font-size: 150%;
  opacity: 0;
  position: absolute;
  animation: fadeAnimation 2s ease-in-out infinite;
}

@keyframes fadeAnimation {
  0%, 100% {
    opacity: 0;
    transform: translateY(10px);
  }
  50% {
    opacity: 1;
    transform: translateY(0);
  }
}
.font-head{
    font-size: 350%;
}
.margin-h1-top{
  margin: 13%;
}
.image-adjusting-top{
    /* width: 50%; */
    border: 1px solid black;
    border-radius: 10%;
}
.image-adjusting-center{
  border: 1px solid black;
  border-radius: 10%;
  margin-top:5%;
} 
.text-ord{
  font-size: 20px;
}
.param{
    width:30%;
}
.fa {
  padding: 20px;
  font-size: 30px;
  width: 50px;
  text-align: center;
  text-decoration: none;
}
.fa:hover {
  opacity: 0.7;
}
